import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import orderBtn from "@/components/order-button.vue";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty,
		orderBtn,
	},
	computed:{
		status(){
			return  this.navList[this.tabCurrentIndex].next ? 'more' : 'noMore'
		}
	},
	data() {
		return {
			tabCurrentIndex: null,
			navList: [{
					state: 0,
					text: '全部',
					filter:'',
					loadingType: 'more',
					orderList: [],
					next:null,
				},
				{
					state: 1,
					text: '待付款',
					filter:'unpaid',
					loadingType: 'more',
					orderList: [],
					next:null,
				},
				{
					state: 2,
					text: '待收货',
					filter:'paid',
					loadingType: 'more',
					orderList: [],
					next:null,
				},
				{
					state: 3,
					text: '已完成',
					filter:'delivered',
					loadingType: 'more',
					orderList: [],
					next:null,
				},
				{
					state: 4,
					text: '已关闭',
					filter:'close',
					loadingType: 'more',
					orderList: [],
					next:null,
				}
			],
		}
	},
	filters: {
	    status(val) {
	      switch (val) {
	        case "unpaid":
	          return "待付款";
	        case "paid":
	          return "配送中";
	        case "delivered":
	          return "已完成";
	        case "close":
	          return "已关闭";
	      }
	    }
	},
	onLoad(){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = 0;
		this.loadData();
	}, 
	onShow() {
		this.loadData();
	},
	methods: {
		loadMore(){
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			if (navItem.orderList.length == 0){
				this.loadData()
			}else if(navItem.next){
				this.$requests.page(navItem.next).then(data=>{
					data.next ? this.next = data.next: this.next = null,
					data.results.forEach(item=>{
						item.order_goods.forEach(item=>{
							sum += item.amount
						})
						item['sum'] = sum
						this.navItem.orderList.push(item)
					})
				})
			}
		},
		returnDate(e){
			if(e.success){
				this.$api.msg(e.msg)
				this.loadData()
			}else{
				this.$api.msg(e.msg)
				this.loadData()
			}
		},
		//获取订单列表
		loadData(){
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let filter = navItem.filter;
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			this.$api.payment.getOrders(filter).then(data =>{
				console.log(data)
					navItem.loadingType = 'loaded'
					navItem.orderList = data.results
					navItem.orderList.map(item=>{
						let sum = 0
						item.order_goods.forEach(item=>{
							sum += item.amount
						})
						item['sum'] = sum
					})
					navItem.loaded = true
					data.next ? this.next = data.next: this.next = null 
					})
		}, 
		goDetail(id){
			uni.navigateTo({
				url: '/pages/order/orderDetail?id='+id
			})
		},
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData();
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
			this.loadData();
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
